.product{
    border: solid black;
    padding: 10px;

    display: flex;
    flex-direction: column;
    align-items: center;
    background-color: white;
}
.cross{
    text-decoration: line-through;
}
.image{
    width: 300px;
}
.rating{
    display: flex;
    gap: 0px;
}
.add-to-cart{
    display: flex;
    
    justify-content: center;
}
.add-to-cart >button{
    background-color: #f0c14b;
    width: 200px;
    height: 30px;
    border: 1px solid;
    border-color: #a88734 #9c7e31 #846a29;
    border-radius: 5px;
}
.no-of{
    display: flex;
    gap: 10px;
    padding: 5px;
}