
.login-page{
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 10px;
}
.login-logo{
    color: black;
    margin-top: 20px;
}
.login-form{
    margin-top: 20px;
    align-items: center;
    border: solid 1px black;
    width: 350px;
    padding: 10px;
    box-shadow: 0px 0px 7px ;
}
.user-name>input{
    width: 80%;
    padding: 3px;
    font-size: 15px;
    border: 1px solid;
}
.password>input{
    padding: 3px;
    font-size: 15px;
    border: 1px solid;
}
.text-area{
    width: 80%;
}
.login-btn>input{
    margin-top: 20px;
    width: 100%;
    height: 25px;
    background-color: #f0c14b;
    border: 1px solid;
    border-color: #a88734 #9c7e31 #846a29;
    /* border-radius: 5px; */
}
.new-account{
    text-align: center;
}
.show-hide{
    font-size: 10px;
}

