.Header{
    display: flex;
    background-color:black;
    color: white;
    align-items: center;
    gap: 10px;
    position: sticky;
    top: 0px;
    z-index: 100;
}
.links{
    text-decoration: none;
    color: white;
}
.logo{
    cursor: pointer;
}
.main-title{
    color: aliceblue;
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
    padding: 0px;
    margin: 0px;
}
.sub-title{
    color: azure;
    margin-top: -5px;
}
.search{
    display: flex;
    flex: 1;
}
.search-bar{
    height: 25px;
    padding: 10px;
    border: none;
    width: 100%;
    font-size: 15px;
}
.search-btn{
    /* height: 22px !important; */
    background-color: #cd9042;
    cursor: pointer;
}
.header-links{
    display: flex;
    justify-content: space-evenly;
    gap: 10px;
}
.header-options{
    display: flex;
    flex-direction: column;
    cursor: pointer;
}
.header-optionLineOne{
    font-size: 10px;
}
.header-optionLineTwo{
    font-size: 13px;
    font-weight: 800;
}
.cart{
    margin-right: 5px;
    display: flex;
    align-items: center;
    cursor: pointer;
}