.content{
    display: flex;
    flex-direction: column;
    align-items: center;
}
.poster{
    z-index: -1;

}
.posters>img{
    width: 1000px;
    aspect-ratio: 6/3;
    object-fit: contain;
    mask-image: linear-gradient(to bottom,rgba(0, 0, 0, 1),rgba(0,0,0,0));
    margin-bottom: -200px;
    z-index: -1;
}
.items{
    width: 100%;
}
.pratices{
    display: flex;
    flex-direction: column;
    align-items: center;
}
    /* ************************************************************** */
    .card{
        aspect-ratio: 5/7;
        background-color: rgb(0, 0, 0,0.1);
        position:absolute;
        top:370%;
        left: 40%;
        transition: 700ms cubic-bezier(0.075, 0.82, 0.165, 1);
    }
    .Bigbox{
        width: 30vmin;
    }
    .Smallbox{
        width: 20vmin;
        top: 370%;
        left: 45%;
        position: absolute;
    }
    .Bigbox>img{
        aspect-ratio: 5/7;
        width: 30vmin;

    }
    .Smallbox>img{
        aspect-ratio: 5/7;
        width: 20vmin;
    }
    Bigbox:nth-child(8){
            
        transform: translate(10%) rotate(5deg);
        background-image: url('');
        
        background-repeat: no-repeat;
        background-size: cover;
        background-position: center;
    }
    .Bigbox:nth-child(6){
        transform: translate(-1%) rotate(-1deg);
        background-image: url('');
    
        background-repeat: no-repeat;
        background-size: cover;
        
    }   
    .Bigbox:nth-child(4){
        transform: translate(-7%) rotate(-5deg);
        background-image: url();
        background-repeat: no-repeat;
        background-size: cover;
        background-position: center;
    }
    .Bigbox:nth-child(2){
        transform: translate(-14%) rotate(-10deg);
        background-image: url();
        background-repeat: no-repeat;
        background-size: cover;
        background-position: center;
    }
    
    .food:hover > .Bigbox:nth-child(2){
        transform: translate(70%,1%) rotate(13deg);
    }
    .food:hover > .Bigbox:nth-child(4){
        transform: translate(20%,-15%) rotate(7deg);
    }
    .food:hover > .Bigbox:nth-child(6){
        transform: translate(-20%,-15%) rotate(-7deg);
    }
    .food:hover > .Bigbox:nth-child(8){
        transform: translate(-70%,1%) rotate(-13deg);
    }
    .food:hover > .Smallbox:nth-child(1){
        transform: translate(-100%,-100%) rotate(-12deg);
        background-image: url();
        background-repeat: no-repeat;
        background-size: cover;
    }
    .food:hover > .Smallbox:nth-child(3){
        transform: translate(100%,-100%) rotate(15deg);
        background-image: url();
        background-repeat: no-repeat;
        background-size: cover;
        background-position: center;
    }
    .food:hover > .Smallbox:nth-child(5){
        transform: translate(100%,100%) rotate(0deg);
        background-image: url();
        background-repeat: no-repeat;
        background-size: cover;
        /* background-position: center; */
    }
    .food:hover > .Smallbox:nth-child(7){
        transform: translate(-100%,100%) rotate(10deg);
        background-image: url();
        background-repeat: no-repeat;
        background-size: cover;
        background-position: center;
    }
    .pratices{
        margin-top: 600px;
    }