.nav{
    background-color: #232f3e;
    color: white;
    width: 100%;
}
.nav-list{
    display: flex;
    gap: 30px;
    list-style: none;
    margin: 0px;
    padding: 0px;
    padding-left: 5px;
}
.nav-list>li{
    display: block;
    padding: 10px 10px;
    text-decoration: none;
    color: white;

}
.nav-list>li:hover{
    background-color: rgb(56, 61, 61);
    cursor: pointer;
}
